"use client";

import React from "react";
import Link from "next/link";
import { Crown, Building, Users, ArrowRight, Sparkles } from "lucide-react";
import { Button } from "@/components/ui/button";

export default function SelectRolePage() {
    const roles = [
        {
            id: "organizer",
            title: "Organizer",
            description: "Host luxury galas, manage guest lists, and curate world-class experiences.",
            icon: Building,
            color: "amber",
            path: "/auth/sign-up?role=organizer"
        },
        {
            id: "attendee",
            title: "Attendee",
            description: "Discover private events, secure premium tickets, and join the elite inner circle.",
            icon: Users,
            color: "blue",
            path: "/auth/sign-up?role=attendee"
        }
    ];

    return (
        <div className="flex flex-col items-center justify-center min-h-screen px-6 py-12">
            <div className="w-full max-w-4xl space-y-12">
                {/* Header */}
                <div className="text-center space-y-4">
                    <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-amber-500/10 border border-amber-500/20 text-amber-500 text-sm font-medium">
                        <Crown className="w-4 h-4" />
                        <span>Exclusive Access</span>
                    </div>
                    <h1 className="text-5xl md:text-6xl font-black tracking-tighter">Choose Your <span className="text-gradient-gold uppercase">Legacy</span></h1>
                    <p className="text-muted-foreground text-xl font-light max-w-2xl mx-auto">
                        Whether you are curating the event or experiencing it, your journey starts with a choice.
                    </p>
                </div>

                {/* Role Cards */}
                <div className="grid md:grid-cols-2 gap-8">
                    {roles.map((role) => (
                        <Link
                            key={role.id}
                            href={role.path}
                            className="group relative block"
                        >
                            <div className="h-full bg-card/40 backdrop-blur-md border border-white/5 rounded-[2.5rem] p-10 transition-all duration-500 hover:border-amber-500/50 hover:shadow-[0_0_50px_rgba(245,158,11,0.1)] overflow-hidden">
                                {/* Decorative Gradient */}
                                <div className="absolute -right-16 -top-16 w-64 h-64 bg-amber-500/5 rounded-full blur-3xl group-hover:bg-amber-500/10 transition-all duration-700" />

                                <div className="relative z-10 space-y-8">
                                    <div className="w-20 h-20 rounded-3xl bg-amber-500/10 flex items-center justify-center border border-amber-500/20 group-hover:scale-110 group-hover:bg-amber-500/20 transition-all duration-500">
                                        <role.icon className="w-10 h-10 text-amber-500" />
                                    </div>

                                    <div className="space-y-4">
                                        <h2 className="text-3xl font-bold text-white flex items-center gap-2">
                                            {role.title}
                                            <Sparkles className="w-5 h-5 text-amber-500 opacity-0 group-hover:opacity-100 transition-opacity" />
                                        </h2>
                                        <p className="text-muted-foreground text-lg leading-relaxed font-light">
                                            {role.description}
                                        </p>
                                    </div>

                                    <div className="flex items-center gap-2 text-amber-500 font-bold group-hover:gap-4 transition-all pointer-events-none">
                                        Continue <ArrowRight className="w-5 h-5" />
                                    </div>
                                </div>
                            </div>
                        </Link>
                    ))}
                </div>

                <div className="text-center">
                    <p className="text-muted-foreground">
                        Already have an account?{" "}
                        <Link href="/auth/login" className="text-amber-500 font-bold hover:underline">Sign in here</Link>
                    </p>
                </div>
            </div>
        </div>
    );
}
